	import uniSearchBar from '@/components/uni-search-bar/uni-search-bar.1.vue'
	import uniSection from '@/components/uni-section/uni-section.vue'
    import {
        mapState
    } from 'vuex'

    export default {
        computed: mapState(['forcedLogin', 'hasLogin', 'userName']),
		components: {
			uniSearchBar,
			uniSection
		},
		data() {
			return {
				searchVal: ''
			}
		},
		methods: {
			searchClick() {
				this.searchVal = ""
				this.showf = true;
				this.$nextTick(()=>{
					this.showSync = true;
				})
			},
			search(res) {
				uni.showToast({
					title: '搜索：' + res.value,
					icon: 'none'
				})
			},
			input(res) {
				this.searchVal = res.value
			},
			cancel(res) {
				uni.showToast({
					title: '点击取消，输入值为：' + res.value,
					icon: 'none'
				})
			}
		},
		onBackPress() {



		},
        onLoad() {
            
        }
    }
